.bandeAnnonce {
    margin-top: 55px;
    margin-left: 20px;
    position: relative;
}

.splide__slide iframe {
    //Chaque Slide (donc chaque vidéo)
    box-sizing: border-box; 
    max-width: 100% ; 
    border-radius: 35px;
    width: 100%;
    height: auto;
    aspect-ratio: 16/9; /* Assure que l'iframe conserve un ratio 16:9 */
   // Lorsqu'il n'y a qu'une seule vidéo affichée sur mobile, le comportement du carrousel peut ne pas être idéal en raison des dimensions spécifiques de l'iframe ou de la manière dont le carrousel calcule l'espace pour les vidéos.
    overflow: hidden;
}
.splide {
    //Conteneur du carroussel
    background: #060d2c;
    padding: 15px;
    border-radius: 35px;
    margin-bottom: 20px;
}  

.splide__arrow {
    //conteneur de des svg .splide__arrow (les fleches)
    background: #546fe4;
    height: 50px;
    width: 50px;
    border-radius: 15px;
   
}
.splide__arrow svg {
    fill: $white;
    width: 40px; 
    height: 40px;
    
}
  
//Pour styliser les flèches du carrousel Splide, je dois utiliser la propriété fill pour modifier la couleur de l'icône SVG, car les flèches sont souvent des SVGs, et pour eux, la propriété color ne s'applique pas

#jaune {
    position: absolute;
    height: 15px;
    width: 5px;
    background: yellow;
    left: -10px;
    top: 8px;
   
}